html {
  font-style: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
  color: inherit;
  margin: 0;
  bottom: 0;
}

ul {
  list-style-type: none;
}

a {
  text-decoration: none;
}

body {
  max-width: 100vw;
  min-height: 100vh;
  height: 100%;
  font-family: "Barlow", sans-serif;
  position: relative;
}

.btn {
  border: none;
  padding: 0.9375rem 1.5625rem;
  border-radius: 3.125rem;
  cursor: pointer;
  font-family: "Fraunces", serif;
  text-transform: uppercase;
}
.btn.btn-desktop {
  color: #000000;
  background: #ffffff;
}
.btn.btn-desktop:hover {
  background: #6fceff;
  color: #ffffff;
}

.card {
  background-color: salmon;
}

nav {
  position: absolute;
  top: 0;
  top: 2.5rem;
  left: 2.5rem;
  right: 2.5rem;
  height: 2.68125rem;
  display: flex;
  width: 94%;
  justify-content: space-between;
}
nav .nav__logo {
  width: 11.8125rem;
  align-self: flex-start;
}
nav .nav__menu {
  display: flex;
  gap: 3.125rem;
  align-items: center;
}
nav .nav__menu__list {
  color: #ffffff;
  align-self: center;
}

.hero {
  background: url(assets/images/desktop/image-header.jpg);
  background-size: cover;
  height: 100vh;
  padding-top: 28.5714285714vh;
}
.hero__text {
  font-family: "Fraunces", serif;
  font-size: 4rem;
  color: #ffffff;
  text-transform: uppercase;
  letter-spacing: 0.15em;
  margin-bottom: 5rem;
}
.hero__arrow {
  top: 0;
  position: relative;
  transition: all 0.3s ease-in;
}
.hero__arrow:hover {
  top: 1.875rem;
}

.about {
  height: 100vh;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
.about img {
  width: 100%;
}
.about .card-about {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.about .card-about__content {
  width: 100%;
  max-width: 28rem;
}
.about .card-about__content__title {
  font-family: "Fraunces", serif;
  font-size: 2.6875rem;
  margin-bottom: 2.625rem;
  color: #23303e;
}
.about .card-about__content__description {
  color: #818498;
  line-height: 1.6875rem;
  margin-bottom: 1.875rem;
}
.about .link-about {
  font-family: "Fraunces", serif;
  text-transform: uppercase;
  font-weight: 700;
  color: #23303e;
  letter-spacing: 0.12em;
  padding-left: 0.5rem;
  position: relative;
}
.about .link-about--yellow::after {
  content: "";
  width: 9.0625rem;
  height: 0.5rem;
  background: #fad400;
  opacity: 30%;
  position: absolute;
  border-radius: 3.125rem;
  left: 0;
  z-index: -1;
}
.about .link-about--yellow:hover::after {
  opacity: 100%;
}
.about .link-about--red::after {
  content: "";
  width: 9.0625rem;
  height: 0.5rem;
  background: #fe7867;
  opacity: 30%;
  position: absolute;
  border-radius: 3.125rem;
  left: 0;
  z-index: -1;
}
.about .link-about--red:hover::after {
  opacity: 100%;
}

main {
  display: flex;
  align-items: center;
  flex-direction: column;
}